import { SelectionModel } from "@angular/cdk/collections";
import { HttpParams } from "@angular/common/http";
import { Component, OnInit, ViewChild } from "@angular/core";
import { FormGroup, FormBuilder, Validators } from "@angular/forms";
import { MatDialog, MatDialogConfig } from "@angular/material/dialog";
import { MatPaginator } from "@angular/material/paginator";
import { MatSort } from "@angular/material/sort";
import { MatTableDataSource } from "@angular/material/table";
import { Router } from "@angular/router";
import { Subject, takeUntil } from "rxjs";
import { NotificationServiceService } from "src/@core/Models/Notification/notification-service.service";
import { TokenStorageService } from "src/app/core/service/token-storage.service";
import { CcentreLookupComponent } from "src/app/erp-finance/data/lookups/ccentre-lookup/ccentre-lookup.component";
import { KpiGoalService } from "src/app/erp-hr/data/kpi-services/kpi-goal.service";
import { SnackbarService } from "src/app/shared/services/snackbar.service";
import Swal from "sweetalert2";
import { ManageKpiGoalComponent } from "../dialogs/manage-kpi-goal/manage-kpi-goal.component";
import { ReportService } from "src/app/erp-hr/data/payroll-services/report.service";
import { ManageKpiGoalAchievementComponent } from "../dialogs/manage-kpi-goal-achievement/manage-kpi-goal-achievement.component";

@Component({
  selector: "app-kpi-details-page",
  templateUrl: "./kpi-details-page.component.html",
  styleUrls: ["./kpi-details-page.component.sass"],
})
export class KpiDetailsPageComponent implements OnInit {
  displayedColumns: string[] = [
    //"select",
    // "id",
    "empPf",
    "year",
    "quarter",
    "strategicObjective",
    "tacticalPerfomance",
    "isQuantitative",
    "target",
    "weight",
    "goalSubmited",
    "appraisalSubmited",
    "achieved",
    "employeeScore",
    "finalScore",
    "appraisedApprovedBy",
    // "status",
    "action",
  ];

  loading: boolean = false;
  data: any;
  error: any;
  dialogConfig: any;
  destroy$: Subject<boolean> = new Subject<boolean>();

  dataSource!: MatTableDataSource<any>;
  @ViewChild(MatPaginator) paginator!: MatPaginator;
  @ViewChild(MatSort) sort!: MatSort;

  selection = new SelectionModel<any>(true, []);

  downloadLoading: boolean = false;
  posting: boolean = false;
  showForm: boolean = false;

  Form: FormGroup;
  months: string[] = [
    "January",
    "February",
    "March",
    "April",
    "May",
    "June",
    "July",
    "August",
    "September",
    "October",
    "November",
    "December",
  ];

  currentUser: any;

  constructor(
    private dialog: MatDialog,
    private notificationAPI: NotificationServiceService,
    private kpiGoalService: KpiGoalService,
    private snackbar: SnackbarService,
    private fb: FormBuilder,
    private router: Router,
    private tokenStorageService: TokenStorageService,
    private reportAPI: ReportService
  ) {}

  ngOnInit(): void {
    const currentYear = new Date().getFullYear();
    const currentMonthIndex = new Date().getMonth();
    const currentMonthName = this.months[currentMonthIndex];

    this.currentUser = this.tokenStorageService.getUser();

    this.Form = this.fb.group({
      empPf: [this.currentUser.empPfNo, Validators.required],
      year: [currentYear.toString(), Validators.required],
      quarter: ["Quarter 1"],
      status: ["PENDING"],
    });
    this.Form.valueChanges.subscribe(() => {
      this.getData();
    });

    this.getData();
  }

  ngOnDestroy(): void {
    this.destroy$.next(true);
    this.destroy$.complete();
  }

  //************************************************************************************************ */
  reqStatuses: any[] = [
    { name: "ALL" },
    { name: "PENDING" },
    { name: "SUBMITTED" },
    { name: "APPROVED" },
    { name: "RETURNED" },
    { name: "REJECTED" },
    { name: "APPRAISAL" },
    { name: "COMPLETED" },
  ];

  quarterArray: any[] = ["Quarter 1", "Quarter 2", "Quarter 3", "Quarter 4"];

  selectedStatus: string = "ALL";

  getSelectSelectedStatus() {
    //this.getData();
  }
  getSelectedStatus(status: any) {
    this.Form.patchValue({
      status: status,
    });

    //this.getData();
  }
  getData() {
    this.loading = true;
    this.dataSource = new MatTableDataSource([]);
    this.dataSource.paginator = this.paginator;
    this.dataSource.sort = this.sort;

    this.selectedStatus = this.Form.value.status;

    let params = new HttpParams()
      .set("empPf", this.Form.value.empPf)
      .set("year", this.Form.value.year)
      .set("quarter", this.Form.value.quarter)
      .set("status", this.Form.value.status);

    console.log("this.params:: ", params);

    this.kpiGoalService
      .getEmployeeKPIsData(params)
      .pipe(takeUntil(this.destroy$))
      .subscribe({
        next: (res) => {
          if (res.statusCode === 302) {
            this.data = res.entity;
            console.log("KPI Goals displayed on table", this.data);

            this.loading = false;
            this.dataSource = new MatTableDataSource(this.data);
            this.dataSource.paginator = this.paginator;
            this.dataSource.sort = this.sort;
          } else {
            this.loading = false;
          }
        },
        error: (err) => {
          this.loading = false;
          this.notificationAPI.alertWarning("Server Error:  " + err.message);
        },
        complete: () => {},
      });
  }
  refresh() {
    this.getData();
  }

  mngRecord(action: any, record?: any) {
    console.log("this.action:; ", action);
    console.log("this.record:; ", record);

    if (action === "Upload Results") {
      const dialogConfig = new MatDialogConfig();
      dialogConfig.disableClose = false;
      dialogConfig.disableClose = true;
      dialogConfig.width = "800px";
      dialogConfig.data = {
        action: action,
        record: record,
      };
      const dialogRef = this.dialog.open(
        ManageKpiGoalAchievementComponent,
        dialogConfig
      );
      dialogRef.afterClosed().subscribe((result) => {
        console.log("result:::", result);

        this.getData();
      });
    } else {
      const dialogConfig = new MatDialogConfig();
      dialogConfig.disableClose = false;
      dialogConfig.disableClose = true;
      dialogConfig.width = "800px";
      dialogConfig.data = {
        action: action,
        record: record,
      };
      const dialogRef = this.dialog.open(ManageKpiGoalComponent, dialogConfig);
      dialogRef.afterClosed().subscribe((result) => {
        console.log("result:::", result);

        this.getData();
      });
    }
  }

  deleteRecord(id: number) {
    Swal.fire({
      title: "Are you sure?",
      text: "RECORD WILL BE DELETED PERMANENTLY!!",
      icon: "warning",
      showCancelButton: true,
      confirmButtonColor: "#3085d6",
      cancelButtonColor: "#d33",
      confirmButtonText: "Yes, delete it!",
    }).then((result) => {
      if (result.isConfirmed) {
        this.loading = true;
        this.kpiGoalService
          .deleteKPI(id)
          .pipe(takeUntil(this.destroy$))
          .subscribe({
            next: (res) => {
              if (res.statusCode == 200) {
                this.getData();
                this.loading = false;
                this.notificationAPI.alertSuccess(
                  "RECORD DELETED SUCCESSFULLY"
                );
              } else {
                this.getData();
                this.loading = false;
                this.notificationAPI.alertWarning(res.message);
              }
            },
            error: (err) => {
              this.loading = false;
              this.notificationAPI.alertSuccess("Server Error: " + err.message);
            },
            complete: () => {},
          });
      }
    });
  }

  input: any;
  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.input = filterValue;
    this.dataSource.filter = filterValue.trim().toLowerCase();
    if (this.dataSource.paginator) {
      this.dataSource.paginator.firstPage();
    }
  }
  //******************************************************************************************************************************** */
  onCostCenterLookup(type: any) {
    if (type === "Search") {
      const dialogConfig = new MatDialogConfig();
      dialogConfig.disableClose = false;
      dialogConfig.disableClose = true;
      dialogConfig.width = "800px";
      dialogConfig.data = {
        action: "SingleCostCenter",
      };
      const dialogRef = this.dialog.open(CcentreLookupComponent, dialogConfig);
      dialogRef.afterClosed().subscribe((res) => {
        if (res && res.data && res.data.length > 0) {
          this.Form.patchValue({
            costCenterCode: res.data[0].costCenterCode,
          });
        }
      });
    } else if (type === "All") {
      this.Form.patchValue({
        costCenterCode: "All",
      });

      this.getData();
    }
  }
  // ****************************************************************************************************************************
  onSubmit() {
    Swal.fire({
      title: "Are you sure?",
      text: "Are you sure you want to submit the goals for this quarter?",
      icon: "warning",
      showCancelButton: true,
      confirmButtonColor: "#3085d6",
      cancelButtonColor: "#d33",
      confirmButtonText: "Yes, submit!",
      cancelButtonText: "No, cancel",
    }).then((result) => {
      if (result.isConfirmed) {
        let params = new HttpParams()
          .set("empPf", this.Form.value.empPf)
          .set("quarter", this.Form.value.quarter);
        this.kpiGoalService.submit(params).subscribe(
          (res) => {
            if (res.statusCode === 200) {
              this.notificationAPI.alertSuccess(res.message);
              this.getData();
            } else {
              this.notificationAPI.alertWarning(res.message);
            }
          },
          (err) => {
            this.notificationAPI.alertWarning("Server error: " + err.message);
          }
        );
      }
    });
  }

  // ****************************************************************************************************************************

  downloadingQuarterly: boolean = false;
  downloadQuarterlyKpiGoals() {
    this.downloadingQuarterly = true;
    const params = new HttpParams()
      .set("empPf", this.Form.value.empPf)
      .set("year", this.Form.value.year)
      .set("quarter", this.Form.value.quarter)
      .set("status", this.selectedStatus);
    this.reportAPI.employeeKPIQuarterly(params).subscribe({
      next: (value) => {
        this.downloadingQuarterly = false;
        this.notificationAPI.alertSuccess("Downloading KPI Report");
        let url = window.URL.createObjectURL(value.data);
        let a = document.createElement("a");
        document.body.appendChild(a);
        a.setAttribute("style", "display: none");
        a.setAttribute("target", "blank");
        a.href = url;
        a.download = value.filename;
        a.click();
        window.URL.revokeObjectURL(url);
        a.remove();
      },
      error: (err) => {
        this.downloadingQuarterly = false;
        this.notificationAPI.alertWarning("NO REPORT FOUND");
      },
      complete: () => {},
    });
  }
  downloadingAnnual: boolean = false;

  downloadAnnualyKpiGoals() {
    this.downloadingAnnual = true;
    const params = new HttpParams()
      .set("empPf", this.Form.value.empPf)
      .set("year", this.Form.value.year);
    this.reportAPI.employeeKPIAnnualy(params).subscribe({
      next: (value) => {
        this.downloadingAnnual = false;
        this.notificationAPI.alertSuccess("Downloading Annual KPI Report");
        let url = window.URL.createObjectURL(value.data);
        let a = document.createElement("a");
        document.body.appendChild(a);
        a.setAttribute("style", "display: none");
        a.setAttribute("target", "blank");
        a.href = url;
        a.download = value.filename;
        a.click();
        window.URL.revokeObjectURL(url);
        a.remove();
      },
      error: (err) => {
        this.downloadingAnnual = false;
        this.notificationAPI.alertWarning("NO REPORT FOUND");
      },
      complete: () => {},
    });
  }
}
