import { SelectionModel } from '@angular/cdk/collections';
import { HttpParams } from '@angular/common/http';
import { Component, Inject, OnInit, ViewChild } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { MatDialogRef, MAT_DIALOG_DATA, MatDialog } from '@angular/material/dialog';
import { MatPaginator } from '@angular/material/paginator';
import { MatSort } from '@angular/material/sort';
import { MatTableDataSource } from '@angular/material/table';
import { Subject, takeUntil, Subscription } from 'rxjs';
import { NotificationServiceService } from 'src/@core/Models/Notification/notification-service.service';
import { KpiGoalService } from 'src/app/erp-hr/data/kpi-services/kpi-goal.service';
import { AllJobApplicationsComponent } from 'src/app/erp-hr/modules/jobs/job-applications/all-job-applications/all-job-applications.component';
import { SnackbarService } from 'src/app/shared/services/snackbar.service';

@Component({
  selector: 'app-manage-kpi-goal-achievement',
  templateUrl: './manage-kpi-goal-achievement.component.html',
  styleUrls: ['./manage-kpi-goal-achievement.component.sass']
})
export class ManageKpiGoalAchievementComponent implements OnInit {

  Data: any;

  displayedColumns: string[] = ["index", "filename", "filetype", "action"];

  dataSource!: MatTableDataSource<any>;
  @ViewChild(MatPaginator) paginator!: MatPaginator;
  @ViewChild(MatSort) sort!: MatSort;

  destroy$: Subject<boolean> = new Subject<boolean>();

  selection = new SelectionModel<any>(true, []);

  downloadLoading: boolean = false;
  pageFunction: any = "Add";

  mngForm: FormGroup

  quantitative = 'Y';
  strategicObjective: any;
  tacticalPerfomance: any;
  weight: any;
  maxScore: any;

  constructor(
    public dialogRef: MatDialogRef<AllJobApplicationsComponent>,
    @Inject(MAT_DIALOG_DATA) public data,
    private fb: FormBuilder,
    private snackbar: SnackbarService,
    private kpiGoalService: KpiGoalService,
    private dialog: MatDialog,
    private notificationAPI: NotificationServiceService,


  ) {}

  ngOnInit(): void {
    this.pageFunction = this.data.action;
    this.Data = this.data.record;

    this.quantitative = this.Data.isQuantitative
    this.strategicObjective = this.Data.strategicObjective;
    this.tacticalPerfomance = this.Data.tacticalPerfomance;
    this.weight = this.Data.weight
    this.maxScore = this.weight * 2;

    this.mngForm = this.fb.group({
      id: [this.Data.id],
      nonQuantitativeAchieved: ['N/A'],
      employeeScore: [''],
      quantitativeAchieved: ['0']
    });
  }

  input: any;
  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.input = filterValue;
    this.dataSource.filter = filterValue.trim().toLowerCase();
    if (this.dataSource.paginator) {
      this.dataSource.paginator.firstPage();
    }
  }

  percentageValidator(control) {
    const value = control.value;
    const maxAchieved = this.weight * 2;
    if (value && (isNaN(value) || value < 0 || value > maxAchieved)) {
      this.notificationAPI.alertWarning("You maximum value for this is "+maxAchieved);
      return { 'invalidPercentage': true };
    }
    return null;
  }
  onScore($event){
    this.percentageValidator(this.mngForm.controls.score)
  }


  posting: Boolean = false;
  submit(){
    console.log("this.mngForm.value: ", this.mngForm.value);
    this.posting = true;

    let params = new HttpParams()
    .set('id',this.mngForm.controls.id.value)
    .set('nonQuantitativeAchieved',this.mngForm.controls.nonQuantitativeAchieved.value)
    .set('employeeScore',this.mngForm.controls.employeeScore.value)
    .set('quantitativeAchieved',this.mngForm.controls.quantitativeAchieved.value);
 
    
      this.kpiGoalService
        .appraisal(params)
        .pipe(takeUntil(this.destroy$))
        .subscribe({
          next: (res) => {
            console.log("res: ", res);

            if (res.statusCode == 200) {
              this.snackbar.showNotification("snackbar-success", res.message);
              this.onNoClick();
            } else {
              this.snackbar.showNotification("snackbar-danger", res.message);
            }
          },
          error: (err) => {
            this.snackbar.showNotification("snackbar-danger", err.message);
            this.posting = false;
          },
          complete: () => {
            this.posting = false;
          },
        }),
        Subscription;
  }
  response: any
  onNoClick(): void {
    this.dialogRef.close({ event: "close", data: this.response });
  }
}
