import { Component, Inject, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { MatDialogRef, MAT_DIALOG_DATA, MatDialog } from '@angular/material/dialog';
import { Subject, takeUntil, Subscription } from 'rxjs';
import { TokenCookieService } from 'src/app/core/service/token-storage-cookies.service';
import { TokenStorageService } from 'src/app/core/service/token-storage.service';
import { KpiGoalService } from 'src/app/erp-hr/data/kpi-services/kpi-goal.service';
import { AllEmployeesComponent } from 'src/app/erp-hr/modules/employees/all-employees/all-employees.component';
import { FilesService } from 'src/app/shared/services/files.service';
import { SnackbarService } from 'src/app/shared/services/snackbar.service';

@Component({
  selector: 'app-manage-kpi-goal',
  templateUrl: './manage-kpi-goal.component.html',
  styleUrls: ['./manage-kpi-goal.component.sass']
})
export class ManageKpiGoalComponent implements OnInit {

  mngForm: FormGroup;

  destroy$: Subject<boolean> = new Subject<boolean>();

  downloadLoading: boolean = false;

  pageFunction: string = "Add";

  hideSubmit: boolean = false;
  isLoading: boolean = true;

  quarterArray: any[] = ['Quarter 1','Quarter 2','Quarter 3','Quarter 4']


  constructor(
    public dialogRef: MatDialogRef<AllEmployeesComponent>,
    @Inject(MAT_DIALOG_DATA) public data,
    private fb: FormBuilder,
    private snackbar: SnackbarService,
    private kpiGoalService: KpiGoalService,
    private dialog: MatDialog,
    private filesService: FilesService,
    private tokenStorageService: TokenStorageService,
  ) {}

  ngOnInit(): void {
    console.log("LOG DATA = ", this.data);
    this.pageFunction = this.data.action;
    this.formData = this.data.record
    this.generateForm(this.formData);

    this.getPage();
  }
  ngAfterViewInit() {
    if (this.data.action !== "Add") {
      this.generateForm(this.formData);
    }
  }

  ngOnDestroy(): void {
    this.destroy$.next(true);
    this.destroy$.complete();
  }

  disableTransactionType: boolean = true;
  getPage() {
    this.disableTransactionType = true;
    console.log("this.data.action :", this.data.action);
    if (this.data.action === "Add") {
      this.isLoading = false;
      this.generateForm(this.data.record);
    } else if (this.data.action === "Update") {
      this.generateForm(this.data.record);
    } else if (this.data.action === "View") {
      this.generateForm(this.data.record);
      this.activateViewMode();
    }
    this.isLoading = false;
  }


  quantitativeChange(event:any){
    if(event.value=='Y'){
      this.mngForm.controls.nonQuantitativeTarget.setValue('');
    }else{
      this.mngForm.controls.quantitativeTarget.setValue('');
    }
  }

  // {
  //   "id": 0,
  //   "empPf": "string",
  //   "departmentCode": "string",
  //   "quarter": "string",
  //   "strategicObjective": "string",
  //   "tacticalPerfomance": "string",
  //   "nonQuantitativeTarget": "string",
  //   "quantitativeTarget": 0,
  //   "weight": 0,
  //   "isQuantitative": "string",
  //   "costcentercode": "string"
  // }
  formData: any;
  generateForm(formData?): void {
    console.log("generateForm formData :: ", formData);
    this.mngForm = this.fb.group({
      departmentCode: [formData?.departmentCode ?? this.tokenStorageService.getUser().departmentCode, Validators.required],
      costcentercode: [formData?.departmentCode ?? this.tokenStorageService.getUser().costCenterCode, Validators.required],
      empPf: [formData?.empPf ?? this.tokenStorageService.getEmppf(), Validators.required],
      isQuantitative: [formData?.isQuantitative ?? 'Y', Validators.required],
      nonQuantitativeTarget: [formData?.nonQuantitativeTarget ?? ''],
      quantitativeTarget: [formData?.quantitativeTarget ?? 0],
      quarter: [formData?.quarter ?? '', Validators.required],
      strategicObjective: [formData?.strategicObjective ?? '', Validators.required],
      tacticalPerfomance: [formData?.tacticalPerfomance ?? '', Validators.required],
      weight: [formData?.weight ?? '', Validators.required]
    });
  }
  

  
  viewMode: boolean = false;
  activateViewMode() {
    this.viewMode = true;
  }

  posting: boolean = false;

  response: any;
  submit() {
    console.log("this.mngForm.value: ", this.mngForm.value);
    this.posting = true;
    if (this.pageFunction === "Add") {
      this.kpiGoalService.create(this.mngForm.value)
        .pipe(takeUntil(this.destroy$))
        .subscribe({
          next: (res) => {
            this.response = res;
            console.log("res: ", res);
            if (res.statusCode == 201) {
              this.snackbar.showNotification(
                "snackbar-success",
                res.message
              );
            } else {
              this.snackbar.showNotification(
                "snackbar-danger",
                res.message
              );
            }
          },
          error: (err) => {
            this.snackbar.showNotification("snackbar-danger", err.message);
          },
          complete: () => {
            this.posting = false;
            setTimeout(() => {
              this.onNoClick();
            }, 3000);
          },
        }),
        Subscription;
    } else if (this.pageFunction === "Update") {
      this.kpiGoalService.update(this.mngForm.value)
        .pipe(takeUntil(this.destroy$))
        .subscribe({
          next: (res) => {
            this.response = res;
            console.log("resres:: ", res);
            if (res.statusCode == 200) {
              this.snackbar.showNotification(
                "snackbar-success",
                res.message
              );
            } else {
              this.snackbar.showNotification(
                "snackbar-danger",
                res.message
              );
            }
          },
          error: (err) => {
            this.snackbar.showNotification("snackbar-danger", err.message);
          },
          complete: () => {
            this.posting = false;
            setTimeout(() => {
              this.onNoClick();
            }, 3000);
          },
        }),
        Subscription;
    }
  }

  onNoClick(): void {
    this.dialogRef.close({ event: "close", data: this.response });
  }

 
}
